.countdown {
  --901: #6b35e8;
  --801: #3f13a4;
  --701: #050505;
  --601: #595959;
  --501: #fafafa;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: var(--701);
  font-family: "Poppins", sans-serif;
  overflow: hidden;
}
.countdown #time {
  display: flex;
  gap: 30px;
  transform: scale(75%);
}
.countdown #time .circle {
  position: relative;
  width: 150px;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.countdown #time .circle svg {
  position: relative;
  width: 150px;
  height: 150px;
  transform: rotate(270deg);
}
.countdown #time .circle svg circle {
  width: 100%;
  height: 100%;
  fill: transparent;
  stroke-width: 8;
  stroke: #282828;
  stroke-linecap: round;
  transform: translate(5px, 5px);
}
.countdown #time .circle svg circle:nth-child(2) {
  stroke: var(--clr);
  stroke-dasharray: 440;
  stroke-dashoffset: 440;
}
.countdown #time div {
  position: absolute;
  text-align: center;
  font-weight: 500;
  color: #fff;
  font-size: 1.5em;
}
.countdown #time div span {
  position: absolute;
  transform: translateX(-50%) translateY(-10px);
  font-size: 0.35em;
  font-weight: 300;
  letter-spacing: 0.1px;
  text-transform: uppercase;
}
.countdown #time .dots {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  z-index: 1000;
}
.countdown #time .dots:before {
  content: "";
  position: absolute;
  top: -3px;
  width: 15px;
  height: 15px;
  background: var(--clr);
  border-radius: 50%;
  box-shadow: 0 0 20px var(--clr), 0 0 60px var(--clr);
}
.countdown .newYear {
  font-size: 8em;
  font-weight: 500;
  color: #fff;
  text-align: center;
  line-height: 0.6em;
  display: none;
}
.countdown .newYear span {
  font-size: 0.5em;
  font-weight: 300;
}

:root {
  --901: #6b35e8;
  --801: #3f13a4;
  --701: #050505;
  --600: hsl(0, 0%, 20%);
  --601: #595959;
  --501: #fafafa;
  --boxActive: #45095d8b;
  --boxBorder: hsla(0, 0%, 100%, 0.425);
  --boxHover: hsla(0, 0%, 100%, 0.06);
  --box-bg: hsl(0, 0%, 8%);
  --box-t: hsl(0, 0%, 60%);
}

.days__body {
  --901: #6b35e8;
  --801: #3f13a4;
  --701: #050505;
  --601: #595959;
  --501: #fafafa;
  font-family: "Roboto", sans-serif;
  background-color: var(--701);
  min-height: 100vh;
  padding-top: 56px;
  padding-left: 78px;
}

header iframe {
  width: 100%;
  overflow: hidden;
}

main .yt-video-contanier {
  display: flex;
  flex-direction: column;
}
main .yt-video-contanier .video-thumbnail {
  border-radius: 10px;
}
main .yt-video-contanier .video-data {
  display: flex;
  height: 80px;
  padding-top: 10px;
}
main .yt-video-contanier .video-data .channel-logo {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}
main .yt-video-contanier .video-data .channel-logo img {
  border-radius: 50%;
}
main .yt-video-contanier .video-data .title-channel-name p:nth-of-type(1) {
  color: #fff;
}
main .yt-video-contanier .video-data .title-channel-name p:nth-of-type(2) {
  color: rgba(255, 255, 255, 0.3294117647);
}

aside,
* {
  scrollbar-width: none;
}
aside::-webkit-scrollbar,
*::-webkit-scrollbar {
  width: 0;
  height: 0;
  background-color: hsla(0deg, 0%, 0%, 0);
}
aside::-webkit-scrollbar-thumb,
*::-webkit-scrollbar-thumb {
  background-color: hsla(0deg, 0%, 0%, 0);
}

aside {
  /*
  aside ↓↓
        ==> [div] .home-svg
        ||    ↓↓
        ||    ==> [a] ==> [svg] ==> [path]
        ||
        ==> [div] .days-container  ( DAY 1 to DAY 100 ) 
              ↓↓
              ==> [div] .day
                    ↓↓
                    ==> [div] .day-number
                          ↓↓
                          ==> [span] (DAY 1..2..3)
  */
  width: 78px;
  padding: 4px;
  height: calc(100% - 56px);
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: var(--701);
  overflow: auto;
}

aside .home-svg {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--801);
  height: 64px;
  width: 64px;
  margin-bottom: 5px;
}
aside .home-svg svg {
  width: 24px;
}

.days-container {
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 2px;
  border-radius: 0;
}

.days-container:hover > .day::after {
  opacity: 1;
}

.day {
  cursor: pointer;
  height: 64px;
  width: 64px;
  display: flex;
  flex-direction: column;
  position: relative;
}

.day:hover::before {
  opacity: 1;
}

.day::before,
.day::after {
  content: "";
  height: 100%;
  width: 100%;
  opacity: 0;
  position: absolute;
  left: 0;
  top: 0;
  transition: opacity 500ms;
}

.day::before {
  background: radial-gradient(400px circle at var(--mouse-x) var(--mouse-y), var(--boxHover), transparent 40%);
  z-index: 3;
}

.day::after {
  background: radial-gradient(400px circle at var(--mouse-x) var(--mouse-y), var(--boxBorder), transparent 30%);
  z-index: 1;
}

.day > .day-number {
  background-color: var(--box-bg);
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  inset: 1px;
  padding: 10px;
  position: absolute;
  z-index: 2;
}
.day > .day-number span {
  color: var(--box-t);
  font-weight: 600;
  font-size: 1rem;
  margin: auto;
  text-align: center;
}

.day > .active {
  background-color: var(--boxActive);
}

.sidebar-menu-open {
  width: 208px;
}
.sidebar-menu-open .days-container {
  margin: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 2px;
}
.sidebar-menu-open .home-svg {
  width: 100%;
}

main section {
  display: block;
  min-height: 300px;
  margin-bottom: 20px;
}

main .statistics-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 5px;
  padding-top: 20px;
}
main .statistics-section img {
  height: 130px;
  border-radius: 5px;
}

main .youtube-section {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 10px;
}

main .projects-update {
  background-color: hsla(273deg, 100%, 50%, 0.025);
  display: grid;
  grid-template-columns: 1fr 2fr;
  column-gap: 10px;
  padding: 20px 0;
}
main .projects-update__media {
  padding: 10px;
}
main .projects-update__media img {
  box-shadow: 0 0 7px 0 var(--801);
  width: 100%;
  border-radius: 2px;
}

.details {
  padding: 10px;
  color: var(--501);
}
.details article .project-title {
  display: flex;
  justify-content: center;
}
.details article .project-title h3 {
  text-shadow: 0 0 1px;
}
.details article .repo-info {
  padding: 20px 0 0;
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
  gap: 5px;
}
.details article h4 {
  margin-block: 20px 0;
}
.details article .repo-new-features {
  padding: 15px 0 10px 12px;
  color: hsla(0deg, 0%, 100%, 0.685);
}
.details article .repo-new-features ul {
  list-style: none;
}
.details article .repo-new-features ul li {
  position: relative;
}
.details article .repo-new-features ul li::before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border: solid 5px transparent;
  border-left-color: var(--901);
  top: 50%;
  transform: translateY(-50%);
  left: -10px;
}

nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 56px;
  background-color: rgba(88, 0, 0, 0.226);
  position: fixed;
  top: 0;
  right: 0;
}
nav .menu-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 72px;
  background-color: green;
  cursor: pointer;
}
nav .menu-btn:hover {
  background-color: rgb(132, 0, 255);
}
nav .menu-btn img {
  width: 24px;
  height: 24px;
}

.navbar {
  position: relative;
  height: 44px;
  border-bottom: solid 1px rgba(255, 255, 255, 0.2);
}
.navbar__inside_contanier {
  height: 100%;
  max-width: 600px;
  margin-inline: auto;
}
.navbar__inside_contanier__list {
  height: 100%;
  list-style: none;
  display: flex;
  justify-content: center;
}
.navbar__inside_contanier__list li {
  display: flex;
  align-items: center;
  height: 100%;
}
.navbar__inside_contanier__list li a {
  padding-left: 10px;
  padding-right: 10px;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.6431372549);
  transition: color 150ms ease-in-out;
  position: relative;
}

.navbar__inside_contanier__list .active-section {
  color: var(--501);
}
.navbar__inside_contanier__list .active-section::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  background-color: rgba(255, 255, 255, 0.6431372549);
  left: 0;
  bottom: -15px;
}
.navbar__inside_contanier__list li:hover a {
  color: var(--501);
}